.Form {
  margin: 0 auto;
  max-width: 500px;
}

.Form__header {
  margin-bottom: 20px
}

.Form__submit_button {
  background-color: #00a461;
  border: 2px solid #fff;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  font-size: 1.5em;
  padding: 10px 40px;
  margin-top: 30px;
}

.Form__submit_button:hover {
  background-color:#fff;
  border: 2px solid #00a461;
  color: #00a461;
}



